<!-- 公共底图组件 -->
<template>
  <div class="pb-containter">
    <div v-for="(item,index) in PB_MAP" :key="index" @click="handleChangeMap(item)">
      <div :style="{backgroundImage:'url(' + item.imgUrl + ')'}"></div>
      <p>{{item.name}}</p>
    </div>
  </div>
</template>

<script>
import { PB_MAP } from "../../const/enum";
export default {
  //import引入的组件需要注入到对象中才能使用
  components: {},
  data() {
    //这里存放数据
    return {
      PB_MAP
    };
  },
  //监听属性 类似于data概念
  computed: {},
  //监控data中的数据变化
  watch: {},
  //方法集合
  methods: {
    handleChangeMap(item) {
      this.$emit("change-map", item);
    }
  },
  //生命周期 - 创建完成（可以访问当前this实例）
  created() {},
  //生命周期 - 挂载完成（可以访问DOM元素）
  mounted() {},
  beforeCreate() {}, //生命周期 - 创建之前
  beforeMount() {}, //生命周期 - 挂载之前
  beforeUpdate() {}, //生命周期 - 更新之前
  updated() {}, //生命周期 - 更新之后
  beforeDestroy() {}, //生命周期 - 销毁之前
  destroyed() {}, //生命周期 - 销毁完成
  activated() {} //如果页面有keep-alive缓存功能，这个函数会触发
};
</script>
<style lang='less' scoped>
//@import url(); 引入公共css类
.pb-containter {
  color: #fff;
  background: #171d24;
  padding: 16px 8px;
}
.pb-containter > div {
  //background: #213a52;
  display: inline-block;
  margin: 0 8px;
}
.pb-containter > div > div {
  border-radius: 4px;
  cursor: pointer;
  background-size: 126px 90px;
  width: 126px;
  height: 90px;
}
.pb-containter > div > div:hover {
  border: 2px solid rgba(68, 170, 246, 1);
  transition: border 0.5s;
}
.pb-containter > div > p {
  font-size: 12px;
  font-family: Source Han Sans CN;
  font-weight: 400;
  color: rgba(211, 220, 235, 1);
  line-height: 30px;
  height: 30px;
  vertical-align: middle;
  text-align: center;
}
</style>


