import { getHost, getToken } from '../utils/storage'
import store from '@/store';

/**
* @fileOverview 配置文件
* @author LJ
* @version 0.1
*/
const config = {
    //host: host,
    serviceUrl: 'api',
    version: 'v1'
};
/****
 * @description 获取模块请求的url服务地址
 * @param{String} uri 模块子路径
 * @param{Boolean} bToken 是否需要token
 * @returns{String} 返回模块请求的url地址
 */
const formatServiceUrl = (uri, bToken) => {
    const { version, serviceUrl } = config;
    const service = `/${serviceUrl}/${version}/`;
    //let host = getHost();
    //let access_token = getToken();
    let host = store.state.host;
    let access_token = store.state.access_token;
    if (bToken) //需要Toekn
    {
        return host + service + uri + "?access_token=" + access_token;
    }
    else {
        return host + service + uri;
    }
}
export { formatServiceUrl }



