import { Message } from 'iview';

/***
 * @description 成功提示信息
 * @param{String} text 提示内容
 */
export function successMsg(text) {
    Message.success({ content: text, duration: 3 });
}
/***
 * @description 错误提示信息
 * @param{String} text 提示内容
 */
export function errorMsg(text) {
    Message.error({ content: text, duration: 3 });
}
/***
 * @description 提示信息
 * @param{String} text 提示内容
 */
export function infoMsg(text) {
    Message.info({ content: text, duration: 3 });
}



