/**
* @fileOverview 本地存储模块
* @author LJ
* @version 0.1
*/
const TokenKey = 'token';
const HostKey = 'host';
const SeUserKey = 'se_user';
const MapIdKey = 'map_id';
const DatasetIdKey = 'dataset_id';
/***
 * @description 获取用户token
 * @returns{String} 返回用户token
 */
export function getToken() {
  let token = localStorage.getItem(TokenKey);
  return token;
}
/***
 * @description 设置token
 * @returns{String} 返回用户token
 */
export function setToken(token) {
  localStorage.setItem(TokenKey, token);
}
/***
 * @description 移除token
 */
export function removeToken() {
  localStorage.removeItem(TokenKey);
}

/***
 * @description 获取服务URL根地址
 * @returns{String} 返回URL
 */
export function getHost() {
  return localStorage.getItem(HostKey);
}
/***
 * @description 设置服务URL根地址
 * 
 */
export function setHost(host) {
  localStorage.setItem(HostKey, host);
}
/***
 * @description 移除Host
 */
export function removeHost() {
  localStorage.removeItem(HostKey);
}
/***
 * @description 获取当前用户名,唯一
 * @returns{String} 返回URL
 */
export function getUser() {
  return localStorage.getItem(SeUserKey);
}
/***
 * @description 设置当前用户名
 * 
 */
export function setUser(username) {
  localStorage.setItem(SeUserKey, username);
}
/***
 * @description 移除User
 */
export function removeUser() {
  localStorage.removeItem(SeUserKey);
}
/***
 * @description 地图样式ID
 * @returns{String} 返回URL
 */
export function getMapId() {
  return localStorage.getItem(MapIdKey);
}
/***
 * @description 设置当前地图ID
 * 
 */
export function setMapId(id) {
  localStorage.setItem(MapIdKey, id);
}
/***
 * @description 移除地图ID
 */
export function removeMapId() {
  localStorage.removeItem(MapIdKey);
}
/***
 * @description 数据集ID
 * @returns{String} 返回URL
 */
export function getDatasetId() {
  return localStorage.getItem(DatasetIdKey);
}
/***
 * @description 设置数据集ID
 * 
 */
export function setDatasetId(id) {
  localStorage.setItem(DatasetIdKey, id);
}
/***
 * @description 移除数据集ID
 */
export function removeDatasetId() {
  localStorage.removeItem(DatasetIdKey);
}
/****
 * @description 设置全局配置
 */
export function setLocalStorageConfig(token, host, username) {
  setToken(token);
  setHost(host);
  setUser(username);
}
/****
 * @description 移除
 */
export function removeLocalStorageConfig() {
  removeToken();
  removeHost();
  removeUser();
  removeMapId();
  removeDatasetId();
}



