import Vue from 'vue'
import Vuex from 'vuex'
import { getToken, getHost, getDatasetId } from '../utils/storage'
const PB_MAP_DEFAULT = "天地图矢量(4326)";
L.QGIS.MouseStyle.baseImageUrl = "static/lib/images/cursor/";
/***
 * @description 用户token
 */
const access_token = getToken();
/***
 * @description 服务URL
 */
const host = getHost();
/***
 * @description 进入编辑中,数据集ID集合
 */
const dataset = getDatasetId();
/**
* @fileOverview Store模块
* @author LJ
* @version 0.1
*/
Vue.use(Vuex);
const store = new Vuex.Store({
    state: {
        /***
         * @description 地图对象
         */
        map: null,
        /***
         * @description 高德地图根据IP获取对应的城市数据
         */
        city: null,
        /****
         * @description 用户token
         */
        access_token,
        /***
         * @description 矢量平台的服务地址
         */
        host,
        /***
         * @description 当前用户名
         */
        userName: '',
        /****
         * @系统用户名
         */
        sysUser: '',
        /****
         * @description 进入编辑中,数据集ID集合
         */
        datasets: dataset && [dataset],
        curPbMap: PB_MAP_DEFAULT, //公共地图名称唯一天地图矢量(4326)
        gray: 0 //0表示正常;1表示灰度
    },
    mutations: {
        /***
         * @description 设置Map对象
         */
        setMap(state, value) {
            state.map = value;
        },
        /****
         * @description 设置当前城市
         */
        setCity(state, value) {
            state.city = value;
        },
        setApiConfig(state, conf) {
            // state.access_token = conf.token;
            // state.host = conf.host;
            // state.userName = conf.user_name;
            // state.sysUser = conf.sys;
            state.host = conf.host;
            state.access_token = conf.access_token;
        },
        setCurPbMap(state, val) {
            val = val || PB_MAP_DEFAULT;
            state.curPbMap = val;
        },
        setGray(state, val) {
            state.gray = val;
        }
    }
})
export default store;


