import { datasetType, dataType, COLORS } from "../const/enum";
import tinycolor from "tinycolor2";
/***
 * @description 判断字符串长度
 * @param {String} val 字符串
 * @param {Number} len 长度
 * @returns {Boolean} true表示成功某个长度
 */
function isMoreThanLength(val, len) {
  if (val == null) {
    val = "";
  }
  val = val.toString().trim();
  len = len || 64;
  if (len <= 0) {
    len = 64;
  }
  if (val.length > len) {
    return true;
  } else {
    return false;
  }
}
/**
 * @fileOverview util模块
 * @author LJ
 * @version 0.1
 */
/***
 * @description 判断是否为空字符串
 * @returns {Boolean} true表示空字符串
 */
function isValidString(val) {
  if (val != undefined && val != null && val.trim().length > 0) {
    return true;
  } else {
    return false;
  }
}
/***
 * @description 判断是否为浮点数
 * @param{String} val数字字符串
 * @returns{Boolean} 返回验证结果
 */
function isValidDecimal(val) {
  if (val == null || val == undefined) {
    val = "";
  } else {
    val = val.toString();
  }
  // let reg = /^(-?\\d+)(\\.\\d+)?$/;
  var regPos = /^\d+(\.\d+)?$/; //非负浮点数
  var regNeg = /^(-(([0-9]+\.[0-9]*[1-9][0-9]*)|([0-9]*[1-9][0-9]*\.[0-9]+)|([0-9]*[1-9][0-9]*)))$/; //负浮点数
  if (regPos.test(val) || regNeg.test(val)) {
    return true;
  } else {
    return false;
  }
}
/***
 * @description 判断是否为整数
 * @param{String} val数字字符串
 * @returns{Boolean} 返回验证结果
 */
function isValidInteger(val) {
  if (val == null || val == undefined) {
    val = "";
  } else {
    val = val.toString();
  }
  const reg = /^-?\d+$/;
  return reg.test(val);
}
/***
 * @description 判断一个字符串是否为合法的日期格式：YYYY-MM-DD HH:mm:ss
 * @returns {Boolean} true表示合法
 */
function isValidDate(ds) {
  if (ds == null || ds == undefined) {
    val = "";
  }
  var parts = ds.split(" ");
  switch (parts.length) {
    case 2:
      if (isDatePart(parts[0]) && isTimePart(parts[1])) {
        return true;
      } else {
        return false;
      }
    // case 1:
    //   var aPart = parts[0];
    //   if (aPart.indexOf(':') > 0) {
    //     return isTimePart(aPart);
    //   } else {
    //     return isDatePart(aPart);
    //   }
    default:
      return false;
  }
}
/***
 * @description 判断一个字符串是否为合法的日期格式：YYYY-MM-DD
 * @returns {Boolean} true表示合法
 */
function isDatePart(dateStr) {
  var parts;
  if (dateStr.indexOf("-") > -1) {
    parts = dateStr.split("-");
  } else if (dateStr.indexOf("/") > -1) {
    parts = dateStr.split("/");
  } else {
    return false;
  }
  if (parts.length < 3) {
    //日期部分不允许缺少年、月、日中的任何一项
    return false;
  }
  for (var i = 0; i < 3; i++) {
    //如果构成日期的某个部分不是数字，则返回false
    if (isNaN(parts[i])) {
      return false;
    }
  }
  var y = parseInt(parts[0]); //年
  var m = parseInt(parts[1]); //月
  var d = parseInt(parts[2]); //日
  if (y < 1000 || y > 9999) {
    return false;
  }
  if (m < 1 || m > 12) {
    return false;
  }
  if (d < 1 || d > 31) {
    return false;
  }
  switch (d) {
    case 29:
      if (m == 2) {
        //如果是2月份
        var cond1 = y % 4 == 0; //条件1：年份必须要能被4整除
        var cond2 = y % 100 != 0; //条件2：年份不能是整百数
        var cond3 = y % 400 == 0; //条件3：年份是400的倍数
        var cond = (cond1 && cond2) || cond3;
        if (cond) {
        } else {
          return false;
        }
      }
      break;
    case 30:
      if (m == 2) {
        //2月没有30日
        return false;
      }
      break;
    case 31:
      if (m == 2 || m == 4 || m == 6 || m == 9 || m == 11) {
        //2、4、6、9、11月没有31日
        return false;
      }
      break;
    default:
      break;
  }
  return true;
}
/***
 * @description 判断一个字符串是否为合法的时间格式：HH:mm:ss
 * @returns {Boolean} true表示合法
 */
function isTimePart(timeStr) {
  var parts;
  parts = timeStr.split(":");
  if (parts.length < 2) {
    //日期部分不允许缺少小时、分钟中的任何一项
    return false;
  }
  for (var i = 0; i < parts.length; i++) {
    //如果构成时间的某个部分不是数字，则返回false
    if (isNaN(parts[i])) {
      return false;
    }
  }
  var h = parseInt(parts[0]); //时
  var m = parseInt(parts[1]); //分
  if (h < 0 || h > 23) {
    //限制小时的范围
    return false;
  }
  if (m < 0 || h > 59) {
    //限制分钟的范围
    return false;
  }
  if (parts.length > 2) {
    var s = parseInt(parts[2]); //日
    if (s < 0 || s > 59) {
      //限制秒的范围
      return false;
    }
  }
  return true;
}
/***
 *@description 判断字符是否为空的方法
 *@param{String} obj被监测的字符串
 *@returns{Boolean} 返回验证结果
 */
function isEmpty(obj) {
  if (obj === undefined || obj == null) {
    return true;
  }
  if (obj.trim() == "") {
    return true;
  } else {
    return false;
  }
}
/***
 * @description 把字符串转成数字
 * @param{String} val数字字符串
 * @returns{Number|undefined} 返回结果
 */
function parseNum(val) {
  if (isValidDecimal(val) || isValidInteger(val)) {
    return Number(val);
  } else {
    return null;
  }
}
