/***
 * @description 获取数据集ID
 * @returns{Array} 返回数据集ID数组
 */
function getDatasets() {
  if (
    process &&
    process.env &&
    process.env.NODE_ENV &&
    process.env.NODE_ENV === "development"
  ) {
    return ["fa1ac125-eca5-457a-9a89-98d2f04efffa"];
  } else {
    let temps = localStorage.getItem("datasets");
    if (temps == null || temps == undefined) {
      return [];
    } else {
      return JSON.parse(temps);
    }
  }
}
function createFieldsFromFieldsInfo(fieldInfos) {
  let fields = [];
  fieldInfos &&
    fieldInfos.forEach((item) => {
      let tempType = "";
      switch (item.getType()) {
        case L.SE.DataType.DB_STRING:
        case dataType.STRING: {
          tempType = dataType.STRING;
          break;
        }
        case L.SE.DataType.DB_INTEGER:
        case dataType.BIGINT:
        case dataType.INT: {
          tempType = dataType.INT;
          break;
        }
        case dataType.FLOAT: {
          tempType = dataType.FLOAT;
          break;
        }
        case L.SE.DataType.DB_DOUBLE:
        case dataType.DOUBLE: {
          tempType = dataType.DOUBLE;
          break;
        }
        case L.SE.DataType.DB_DATETIME:
        case dataType.DATETIME: {
          tempType = dataType.DATETIME;
          break;
        }
        case L.SE.DataType.DB_DATE:
        case dataType.DATE: {
          tempType = dataType.DATE;
          break;
        }
        case L.SE.DataType.DB_TIME:
        case dataType.TIME: {
          tempType = dataType.TIME;
          break;
        }
        case L.SE.DataType.DB_BLOB:
        case dataType.BLOB: {
          tempType = dataType.BLOB;
          break;
        }
        case dataType.BOOL: {
          tempType = dataType.BOOL;
          break;
        }
      }
      if (tempType) {
        fields.push({ name: item.getName(), type: tempType });
      }
    });
  return fields;
}
/****
 * @description 根据SeLayer对象创建图层列表的数据
 * @param {SeLayer} layer为SeLayer对象
 * @param {Boolean} 是否为编辑图层
 * @returns {Object} 返回结果
 */
function createLayerInfo(layer, bEdit) {
  if (!layer || layer.dataset == null) {
    return null;
  }
  let dataset = layer.dataset;
  let dtType = "";
  switch (dataset.type) {
    case 1:
    case datasetType.POINT: {
      dtType = datasetType.POINT;
      break;
    }
    case 2:
    case datasetType.LINE: {
      dtType = datasetType.LINE;
      break;
    }
    case 3:
    case datasetType.POLYGON: {
      dtType = datasetType.POLYGON;
      break;
    }
  }
  if (!dtType) {
    //console.log("不支持类型");
    return null;
  }
  let fieldInfos = dataset.fieldInfos;
  let fields = [];
  fieldInfos &&
    fieldInfos.forEach((item) => {
      if (!item.isSysField()) {
        let tempType = "";
        switch (item.getType()) {
          case L.QGIS.DataType.DB_STRING:
          case dataType.STRING: {
            tempType = dataType.STRING;
            break;
          }
          case L.QGIS.DataType.DB_INTEGER:
          case dataType.BIGINT:
          case dataType.INT: {
            tempType = dataType.INT;
            break;
          }
          case dataType.FLOAT: {
            tempType = dataType.FLOAT;
            break;
          }
          case L.QGIS.DataType.DB_DOUBLE:
          case dataType.DOUBLE: {
            tempType = dataType.DOUBLE;
            break;
          }
          case L.QGIS.DataType.DB_DATETIME:
          case dataType.DATETIME: {
            tempType = dataType.DATETIME;
            break;
          }
          case L.QGIS.DataType.DB_DATE:
          case dataType.DATE: {
            tempType = dataType.DATE;
            break;
          }
          case L.QGIS.DataType.DB_TIME:
          case dataType.TIME: {
            tempType = dataType.TIME;
            break;
          }
          case L.QGIS.DataType.DB_BLOB:
          case dataType.BLOB: {
            tempType = dataType.BLOB;
            break;
          }
          case dataType.BOOL: {
            tempType = dataType.BOOL;
            break;
          }
        }
        if (tempType) {
          fields.push({ name: item.getName(), type: tempType });
        }
      }
    });
  let renderStyle = layer.renderStyle ? layer.renderStyle : null;
  let renderItem = renderStyle && renderStyle.getDefaultRenderItem();
  let symbol = renderItem && renderItem.getSymbol();
  let symbolColor = symbol && symbol.getColor().hexValue();
  let lineColor = null;
  if (dtType == datasetType.POLYGON) {
    lineColor = symbol.getOutlineSymbol().getColor().hexValue();
  }
  let renderTextStyle = layer.renderTextStyle ? layer.renderTextStyle : null;
  let field =
    renderTextStyle && renderTextStyle.getUsedFields()
      ? renderTextStyle.getUsedFields()[0]
      : "";
  let textRenderItem =
    renderTextStyle && renderTextStyle.getRenderItem()
      ? renderTextStyle.getRenderItem()
      : null;
  let textSymbol =
    textRenderItem && textRenderItem ? textRenderItem.getTextSymbol() : null;
  let label = {
    enable: renderTextStyle ? renderTextStyle.isEnable() : false,
    field: field,
    size: textSymbol ? textSymbol.getFontSize() : 12,
    color: textSymbol ? textSymbol.getColor().hexValue : "#000000",
  };
  let filter = null;
  if (layer.filter && layer.filter.length > 0) {
    if (layer.filter[0] === "all" || layer.filter[0] === "any") {
      filter = { op: layer.filter[0], list: [] };
      for (let i = 1; i < layer.filter.length; i++) {
        if (layer.filter[i].length == 3) {
          filter.list.push({
            name: layer.filter[i][1],
            operator: layer.filter[i][0],
            value: layer.filter[i][2],
          });
        }
      }
    }
  }
  if (!filter) {
    filter = {
      op: "all", //all或any
      list: [], //每项为{name:字段名称,operator:比较运算符,value:内容}
    };
  }
  let obj = {
    num: 0,
    id: layer.id, //图层ID
    visible: layer.visible,
    type: dtType, //1表示点,2表示线,3表示面
    name: layer.name,
    dataset_id: dataset.id, //数据集ID
    editable: bEdit === undefined ? false : bEdit, //可编辑
    fields: fields, //字段列表,每一项为{name:名称,type:；类型}
    symbol: {
      color: symbolColor, //符号颜色
    },
    label: label,
    filter: filter,
    isSetting: false, //废弃
    opAction: 0, //0表示无操作,1表示设置,2表示属性查询
  };
  if (lineColor) {
    obj.symbol.lineColor = lineColor;
  }
  //filter: {
  //   op: "", //and或any
  //   list: [] //每项为{name:字段名称,operator:比较运算符,value:内容}
  // }
  return obj;
}
/****
 * @description 产生随机整数
 * @param{Number} min最小整数
 * @param{Number} max最大整数
 * @returns{Number} 返回结果 [min,max)
 */
function randomInt(min, max) {
  var range = max - min;
  var rand = Math.random();
  var num = min + Math.floor(rand * range);
  return num;
}
/****
 * @description 产生十六进制颜色随机
 * @returns{String} 返回结果
 *
 */
function randomColor16() {
  var r = Math.floor(Math.random() * 256);
  var g = Math.floor(Math.random() * 256);
  var b = Math.floor(Math.random() * 256);
  var color = "#" + r.toString(16) + g.toString(16) + b.toString(16);
  return color;
}

/****
 * @description 产生符号颜色
 * @param {Array} list 已经存在的颜色
 * @returns {String} 返回结果
 */
function randomSymbolColor(list) {
  list = list || [];
  if (list && list.length >= COLORS.length) {
    //默认颜色已有完
    while (true) {
      let color = randomColor16();
      if (list.indexOf(color) < 0) {
        return color;
      }
    }
  } else {
    let len = COLORS.length;
    while (true) {
      let index = randomInt(0, len);
      if (list.indexOf(COLORS[index]) < 0) {
        return COLORS[index];
      }
    }
  }
}
/***
 * @description 对象深度克隆
 * @param{Object} data 数据
 */
function deepCopy(data) {
  return data ? JSON.parse(JSON.stringify(data)) : {};
}
/***
 * @description 根据数据集ID集合生成添加数据的结构
 * @param {Array} arr数据集ID数组
 * @param {Array} colors本次地图中已存在的颜色
 * @returns {Array} 格式[{id:dtID,color:L.RGBAColor}]
 */
function makeAddDatasets(arr, colors) {
  let list = [];
  arr &&
    arr.forEach((item) => {
      let hexColor = randomSymbolColor(colors);
      colors.push(hexColor);
      let rgb = tinycolor(hexColor).toRgb();
      let color = new L.SymbolLib.RGBAColor(rgb.r, rgb.g, rgb.b, 255);
      list.push({ id: item, color: color });
    });
  return list;
}
/****
 * @description 获取范围根地址
 */
function getBaseUrl() {
  let baseURL = "";
  if (process.env.NODE_ENV == "production") {
    baseURL = "";
  } else {
    baseURL = "http://192.168.1.133:3000";
  }
  return baseURL;
}
var M_PI = 3.14159265358979324;
var a = 6378245.0;
var ee = 0.00669342162296594323;
var x_pi = (M_PI * 3000.0) / 180.0;
function out_of_china(lat, lon) {
  if (lon < 72.004 || lon > 137.8347) {
    return true;
  }
  if (lat < 0.8293 || lat > 55.8271) {
    return true;
  }

  return false;
}
function wgs2gcj_lat(x, y) {
  var ret1 =
    -100.0 +
    2.0 * x +
    3.0 * y +
    0.2 * y * y +
    0.1 * x * y +
    0.2 * Math.sqrt(Math.abs(x));
  ret1 +=
    ((20.0 * Math.sin(6.0 * x * M_PI) + 20.0 * Math.sin(2.0 * x * M_PI)) *
      2.0) /
    3.0;
  ret1 +=
    ((20.0 * Math.sin(y * M_PI) + 40.0 * Math.sin((y / 3.0) * M_PI)) * 2.0) /
    3.0;
  ret1 +=
    ((160.0 * Math.sin((y / 12.0) * M_PI) + 320 * Math.sin((y * M_PI) / 30.0)) *
      2.0) /
    3.0;
  return ret1;
}
function wgs2gcj_lng(x, y) {
  var ret2 =
    300.0 +
    x +
    2.0 * y +
    0.1 * x * x +
    0.1 * x * y +
    0.1 * Math.sqrt(Math.abs(x));
  ret2 +=
    ((20.0 * Math.sin(6.0 * x * M_PI) + 20.0 * Math.sin(2.0 * x * M_PI)) *
      2.0) /
    3.0;
  ret2 +=
    ((20.0 * Math.sin(x * M_PI) + 40.0 * Math.sin((x / 3.0) * M_PI)) * 2.0) /
    3.0;
  ret2 +=
    ((150.0 * Math.sin((x / 12.0) * M_PI) +
      300.0 * Math.sin((x / 30.0) * M_PI)) *
      2.0) /
    3.0;
  return ret2;
}
function wgs2gcj(poi) {
  if (out_of_china(poi.lat, poi.lng)) {
    return poi;
  }
  var poi2 = {};
  var dLat = wgs2gcj_lat(poi.lng - 105.0, poi.lat - 35.0);
  var dLon = wgs2gcj_lng(poi.lng - 105.0, poi.lat - 35.0);
  var radLat = (poi.lat / 180.0) * M_PI;
  var magic = Math.sin(radLat);
  magic = 1 - ee * magic * magic;
  var sqrtMagic = Math.sqrt(magic);
  dLat = (dLat * 180.0) / (((a * (1 - ee)) / (magic * sqrtMagic)) * M_PI);
  dLon = (dLon * 180.0) / ((a / sqrtMagic) * Math.cos(radLat) * M_PI);
  poi2.lat = poi.lat + dLat;
  poi2.lng = poi.lng + dLon;
  return poi2;
}
function isExpressionFilter(filter) {
  if (filter === true || filter === false) {
    return true;
  }

  if (!Array.isArray(filter) || filter.length === 0) {
    return false;
  }
  switch (filter[0]) {
    case "has":
      return filter.length >= 2 && filter[1] !== "$id" && filter[1] !== "$type";

    case "in":
    case "!in":
    case "!has":
    case "none":
      return false;
    case "==":
    case "!=":
    case ">":
    case ">=":
    case "<":
    case "<=":
      return (
        filter.length !== 3 ||
        Array.isArray(filter[1]) ||
        Array.isArray(filter[2])
      );

    case "any":
    case "all":
      for (const f of filter.slice(1)) {
        if (!isExpressionFilter(f) && typeof f !== "boolean") {
          return false;
        }
      }
      return true;

    default:
      return true;
  }
}
function formatNum(num, digits) {
  var pow = Math.pow(10, digits === undefined ? 6 : digits);
  return (Math.round(num * pow) / pow).toFixed(6);
}
function getQueryString(name) {
  var reg = new RegExp("(^|&)" + name + "=([^&]*)(&|$)", "i");
  var r = window.location.search.substr(1).match(reg);
  if (r != null) {
    return unescape(r[2]);
  }
  return null;
}
export default {
  isValidInteger,
  isValidString,
  isValidDecimal,
  isEmpty,
  parseNum,
  getBaseUrl,
  // getToken,
  // getHost,
  getDatasets,
  createLayerInfo,
  randomColor16,
  randomSymbolColor,
  deepCopy,
  makeAddDatasets,
  isValidDate,
  isMoreThanLength,
  isDatePart,
  isTimePart,
  wgs2gcj,
  isExpressionFilter,
  formatNum,
  getQueryString,
  createFieldsFromFieldsInfo,
};



